import { useRef, useState } from "react";
import { useForm } from "react-hook-form";
import InputMask from "react-input-mask";
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";
import emailjs from "@emailjs/browser";


import { StyledPageButton } from "../../../styles/Button";
import { StyledForm } from "./style";
import { Loading } from "../loadingInfo";

export const Form = ({ displayPersonalData, selectedPlan, myForm }) => {
  const anamnese = useRef(null);
  const [anamneseDisplay, setAnamneseDisplay] = useState(false);
  // const [pagamentoDisplay, setPagamentoDisplay] = useState(false);
  const [loadingDisplay, setLoadingDisplay] = useState(false);

  const sendToPayment = () => {

    if (selectedPlan === "anual") {
      window.location.replace(
        process.env.PLANO_ANUAL
      );
    } else if (selectedPlan === "trimestral") {
      window.location.replace(
        process.env.PLANO_TRIMESTRAL
      );
    } else if (selectedPlan === "mensal") {
      window.location.replace(
        process.env.PLANO_MENSAL
      );
    }
  };

  const formSchema = yup.object().shape({
    Nome: yup.string().required("O preenchimento do nome  obrigatrio."),
    DataNascimento: yup
      .string()
      .required("O preenchimento da data de nascimento  obrigatrio."),
    Email: yup
      .string()
      .required("O preenchimento do email  obrigatrio.")
      .email("Digite um email vlido"),
    EmailConfirm: yup
      .string()
      .required("A confirmao do email  obrigatria.")
      .oneOf([yup.ref("Email"), null], "Os emails devem ser iguais"),
    Telefone: yup
      .string()
      .required("O preenchimento do telefone  obrigatrio."),
    Whatsapp: yup.string(),
    Objetivo: yup.string(),
  });

  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm({
    mode: "onBlur",
    resolver: yupResolver(formSchema),
  });

  const submitFunction = async (data) => {
    setLoadingDisplay(true);

    const emailPArams = {
      ...data,
      anamnese: anamneseDisplay,
      plano: selectedPlan,
    };

    try {
      const result = await emailjs.send(
        "service_mdccw6h",
        "template_5gd1jlp",
        emailPArams,
        "0T7hEAqC3nvcNVSqO"
      );

      if (result.text) {
        console.log(result.text);
      }
    } catch (error) {
      console.error(error);
    } finally {
      setTimeout(() => {
        sendToPayment()
        // setLoadingDisplay(false);
      }, 3000);
    }

  };

  return (
    <StyledForm
      viewPersonalData={displayPersonalData}
      viewAnamnese={anamneseDisplay}
      // viewPagamento={pagamentoDisplay}
      ref={myForm}
    >
      {loadingDisplay && <Loading />}
      <form onSubmit={handleSubmit(submitFunction)}>
        <div className="personalData">
          <h3>Dados Pessoais</h3>
          <input type="text" placeholder="Nome" {...register("Nome")} />
          {errors.Nome && (
            <span className="errorMsg">{errors.Nome.message}</span>
          )}
          <span className="birthday" >Data de Nascimento</span>
          <input
            type="date"
            placeholder="Data de Nascimento"
            {...register("DataNascimento")}
          />
          {errors.DataNascimento && (
            <span className="errorMsg">{errors.DataNascimento.message}</span>
          )}
          <input type="text" placeholder="Email" {...register("Email")} />
          {errors.Email && (
            <span className="errorMsg">{errors.Email.message}</span>
          )}
          <input
            type="text"
            placeholder="Confirme seu Email"
            {...register("EmailConfirm")}
          />
          {errors.EmailConfirm && (
            <span className="errorMsg">{errors.EmailConfirm.message}</span>
          )}
          <InputMask
            type="text"
            mask="(99) 99999-9999"
            placeholder="Telefone para contato"
            {...register("Telefone")}
          />
          {errors.Telefone && (
            <span className="errorMsg">{errors.Telefone.message}</span>
          )}
          <label htmlFor="">Este telefone  WhatsApp?</label>
          <select name="Whatsapp" id="" {...register("Whatsapp")}>
            <option value="sim">Sim</option>
            <option value="no">No</option>
          </select>
          <label htmlFor="">Qual  o seu objetivo?</label>
          <select name="Objetivo" id="" {...register("Objetivo")}>
            <option value="Ganho de Massa Muscular">
              Ganho de Massa Muscular
            </option>
            <option value="Perda de gordura">Perda de Gordura</option>
            <option value="Condicionamento fsico">
              Condicionamento Fsico
            </option>
          </select>
          <p>
            Voc pode optar por preencher o formulrio de anamnese agora ou
            posteriormente
          </p>
          <div className="PDButtons">
            <StyledPageButton
              onClick={(e) => {
                e.preventDefault();
                setAnamneseDisplay(true);
                setTimeout(() => {
                  anamnese.current.scrollIntoView();

                }, 50)
              }}
            >
              FAZER ANAMNESE AGORA
            </StyledPageButton>
            <StyledPageButton type="submit">
              IR DIRETO PARA O PAGAMENTO
            </StyledPageButton>
          </div>
        </div>
        <div className="anamnese" ref={anamnese} >
          <h3>Anamnese</h3>
          <input
            type="text"
            pattern="\d*"
            placeholder="Peso (Kg)"
            {...register("Peso")}
          />
          <input
            type="text"
            pattern="\d*"
            placeholder="Altura (cm)"
            {...register("Altura")}
          />
          <input
            type="text"
            pattern="\d*"
            placeholder="Frequncia Cardaca* (bpm)"
            {...register("FrequenciaCardiaca")}
          />
          <span>*Deixar em branco se no souber</span>
          <label htmlFor="">Voc est com sobrepeso?</label>
          <select name="Sobrepeso" id="" {...register("Sobrepeso")}>
            <option value="no">No</option>
            <option value="sim">Sim</option>
          </select>
          <label htmlFor="">Voc fuma?</label>
          <select name="Fuma" id="" {...register("Fuma")}>
            <option value="no">No</option>
            <option value="sim">Sim</option>
          </select>
          <label htmlFor="">
            Possui quaisquer leses ou problemas ortopdicos?
          </label>
          <select
            name="ProblemasOrtpdicos"
            id=""
            {...register("ProblemasOrtpedicos")}
          >
            <option value="no">No</option>
            <option value="sim">Sim</option>
          </select>
          <label htmlFor="">
            Seu mdico aferiu uma P.A. muito alta ou muito baixa?
          </label>
          <select name="PA" id="" {...register("PA")}>
            <option value="no">No</option>
            <option value="sim">Sim</option>
          </select>
          <label htmlFor="">Possui diabticos na famlia?</label>
          <select
            name="HistoricoDiabetes"
            id=""
            {...register("HistoricoDiabetes")}
          >
            <option value="no">No</option>
            <option value="sim">Sim</option>
          </select>
          <label htmlFor="">
            Pratica alguma atividade fsica regularmente?
          </label>
          <select name="AtividadeFsica" id="" {...register("AtividadeFisica")}>
            <option value="no">No</option>
            <option value="sim">Sim</option>
          </select>
          <label htmlFor="">
            Toma algum medicamento prescrito ou suplemento?
          </label>
          <select
            name="Medicamento/Suplemento"
            id=""
            {...register("Medicamento_Suplemento")}
          >
            <option value="no">No</option>
            <option value="sim">Sim</option>
          </select>
          <label htmlFor="">
            Seu mdico lhe recomendou alguma atividade fsica?
          </label>
          <select
            name="RecomendacaoAtividade"
            id=""
            {...register("RecomendacaoAtividade")}
          >
            <option value="no">No</option>
            <option value="sim">Sim</option>
          </select>
          <label htmlFor="">
            Seu nvel de colesterol j esteve muito alto?
          </label>
          <select name="Colesterol" id="" {...register("Colesterol")}>
            <option value="no">No</option>
            <option value="sim">Sim</option>
          </select>
          <label htmlFor="">Possui algum problema cardaco?</label>
          <select name="Cardiaco" id="" {...register("Cardiaco")}>
            <option value="no">No</option>
            <option value="sim">Sim</option>
          </select>
          <StyledPageButton type="submit">
            SEGUIR PARA O PAGAMENTO
          </StyledPageButton>
        </div>
        {/* <div className="pagamento" ref={pgto}>
          <h3>Pagamento</h3>
          <p>
            Voc est a um passo de fazer parte do Carlos Ferreira Team e
            conquistar o melhor fsico da sua vida.
          </p>
          <p>Para concluir, clique no boto abaixo.</p>
          <StyledPageButton onClick={(e) => handleClick(e)}>
            PAGAMENTO
          </StyledPageButton>
        </div> */}
      </form>
    </StyledForm>
  );
};
